import React, { useEffect, useState } from "react";
import ReactPlayer from "react-player";
import { Link, useParams } from "react-router-dom";
import { Typography, Box, Stack } from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import { fetchFromAPI } from "../helper/api-info.js";
import ThumbUpOutlinedIcon from "@mui/icons-material/ThumbUpOutlined";
import VisibilityOutlinedIcon from "@mui/icons-material/VisibilityOutlined";
import { Card, CardMedia, CardActionArea, CardContent } from "@mui/material";
import {
  videoData,
  demoThumbnailUrl,
  demoVideoUrl,
  demoVideoTitle,
  demoChannelUrl,
  demoChannelTitle,
} from "../helper/constants";
import RecommendedVideoCard from "./RecommendedVideoCard";

const VideoInformation = () => {
  const [videoDetail, setVideoDetail] = useState(null);
  const [videos, setVideos] = useState(null);
  const { videoId } = useParams();

  useEffect(() => {
    fetchFromAPI(`videos?part=snippet,statistics&id=${videoId}`)
      .then((data) => {
          setVideoDetail(data.items[0])
          //console.log(data.items[0])
      })

      fetchFromAPI(`search?part=snippet,statistics&relatedToVideoId=${videoId}&type=video`)
      .then((data) => {
          setVideos(data.items);
        })
    
    //setVideos(videoData);
    //setVideoDetail(videoData[0]);
    
  }, [videoId]);

  const title = !!videoDetail ? videoDetail.snippet.title : "PlayTube Title";
  const channelTitle = !!videoDetail ? videoDetail.snippet.channelTitle : "PlayTube Channel Title";
  const viewCount = !!videoDetail ? videoDetail.statistics.viewCount : "2457855";
  const likeCount = !!videoDetail ? videoDetail.statistics.likeCount : "234";

  if (!!videoDetail && !videoDetail.snippet) {
    return <>Loading .....</>;
  }
  return (
    <Box sx={{ background: "#f7f6f2", width: "100%" }}>
      <Stack
        direction={{ xs: "column", md: "row" }}
        sx={{ display: "flex", flexDirection: "row", justifyContent: "center" }}
      >
        <Box
          sx={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "center",
          }}
        >
          <Box
            sx={{
              width: "100%",
              position: "sticky",
              top: "86px",
              padding: "1.3rem",
            }}
          >
            <ReactPlayer
              url={`https://www.youtube.com/watch?v=${videoId}`}
              className="react-player"
              controls
              width={1000}
              height={510}
            />
            <Typography
              color="black"
              variant="h5"
              fontWeight="bold"
              sx={{
                fontSize: "1.28rem",
                fontWeight: "100",
                padding: "16px 0 0",
              }}
            >
              {title}
            </Typography>
            <Stack
              direction="row"
              sx={{
                color: "black",
                padding: "16px 0",
                borderBottom: "1px solid #e9e9e9",
              }}
            >
              <Link
                to="/"
                style={{
                  marginRight: "10px",
                  textDecoration: "none",
                  fontSize: "1.1rem",
                  border: "1px solid black",
                  borderRadius: "10px",
                  padding: "5px",
                }}
                //to={`/channel/${channelId}`}
              >
                <Typography
                  variant={{ sm: "subtitle1", md: "h6" }}
                  color="black"
                >
                  {channelTitle}
                  <CheckCircleIcon
                    sx={{ fontSize: "12px", color: "black", ml: "5px" }}
                  />
                </Typography>
              </Link>
              <Stack direction="row" gap="20px" alignItems="center">
                <Typography variant="body1" sx={{ display: "flex" }}>
                  <VisibilityOutlinedIcon sx={{ marginRight: "10px" }} />
                  {parseInt(viewCount).toLocaleString() ||'2345'} views...
                </Typography>
                <Typography variant="body1" sx={{ display: "flex" }}>
                  <ThumbUpOutlinedIcon sx={{ marginRight: "10px" }} />
                  {parseInt(likeCount).toLocaleString()} likes
                </Typography>
              </Stack>
            </Stack>
          </Box>
          <Box sx={{ marginTop: "1.3rem" }}>
            {!!videos &&
              videos.map((item, idx) => {
                return (
                  <>
                    <Box
                      key={`item${idx}`}
                      sx={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                        flexWrap: "wrap",
                        marginBottom: "1rem",
                      }}
                    >
                      <RecommendedVideoCard searchCard={false} videoDetailInfo={item} />
                    </Box>
                  </>
                );
              })}
          </Box>
        </Box>
      </Stack>
    </Box>
  );
};

export default VideoInformation;
